<template>
  <div class="musicVisual">
    <div class="music-visual" ref='musicVis'>
        <img :src="musicData[currentMusicImgIndex].musicImg" ref='img'>
        <canvas ref='can' class="can"></canvas>
    </div>
    
  </div>
</template>
 
<script>
export default {
    name:'MusicVisual',
    data(){
        return{
            currentMusicImgIndex:0,
        }
    },
    props:["musicData"],
    methods:{
        musicPlaying(){  // 音频可视化
            this.imgRotate = true
            this.imgRotateStop =false
            const audioCtx = new AudioContext()
            const analyser = audioCtx.createAnalyser()
            const source = audioCtx.createMediaElementSource(audio)

            source.disconnect();
            source.connect(analyser)
            analyser.connect(audioCtx.destination)

            analyser.fftSize = 512
            let bufferlength = analyser.fftSize
            let dataArray = new Uint8Array(bufferlength)
            const draw = ()=>{
                requestAnimationFrame(draw)
                analyser.getByteFrequencyData(dataArray)
                this.ctx.clearRect(0,0,this.can.width, this.can.height)
                let width = this.can.width / bufferlength * 10 / 3

                for(let i = 0; i<bufferlength; i++){
                    let height = dataArray[i] / 2
                    let y = this.can.height - height
                    let x = i * width + this.can.width / 2
                    let x2 = this.can.width / 2 - (i + 1) * width

                    const linearGradient = this.ctx.createLinearGradient(0, 80, 0, this.can.height);
                    linearGradient.addColorStop(0, 'white');
                    linearGradient.addColorStop(1, '#7eb9f2');
                    this.ctx.fillStyle = linearGradient
                    this.ctx.fillRect(x, y, width - 4, height)
                    this.ctx.fillRect(x2, y, width - 4, height)
                }
            }
            draw() 
        },
    },

    mounted() {
        this.can = this.$refs.can
        this.ctx = this.can.getContext("2d")

        this.$bus.$on('playMusic',this.musicPlaying)
        this.$bus.$on('getCurrentMusicIndex',(data)=>{
            this.currentMusicImgIndex = data
        })
        this.$bus.$on('playMusic',(data)=>{
            this.imgRotate = data
        })
        
        this.$nextTick(() => {
            const observe = new ResizeObserver(entries => {
                for (const entry of entries) {
                    const target = entry.target;
                    const canvas = target.querySelector('.can');
                    const img = target.querySelector('img');
                    if (entry.contentBoxSize) {
                        canvas.width = entry.contentRect.width - img.offsetWidth;
                        canvas.height = entry.contentRect.height;
                    }
                }
            });
        observe.observe(this.$refs.musicVis);
    });
  }, 
}
</script>

<style scoped lang='less'>
    .musicVisual{
        // height:20vh;
        height:180px;
        width:100%;
        background: #00000050;
        font-size:0;
        border-radius:20px;
        overflow: hidden;
        position:relative;
        margin-bottom: 1vw;
        // flex: 0.4;

        .music-visual{
            height:100%;
            width:100%;
            display: flex;
            img{
                height: 100%;
                aspect-ratio: 1;
                border-radius: 50%;
                margin-right: 10px;
            }
            .can{
                height:100%;
                width:80%;
                // background: #baf;
                flex:1;
            }
        }
    }
    #audio{
        position: absolute;
        left:0;
        top:0;
    }
</style>