<template>
  <div class="MusicListAndLrc">
        <div class="boxTop">
            <div class="musicList">播放列表</div>
            <div class="musicTitle">{{musicData[currentMusicIndex].musicTitle}}</div>
        </div>
        <div class="listAndLrc">
            <Musiclists :musicData='musicData'/>
            <MusicLrc :musicData='musicData'/>
        </div>
        
  </div>
</template>

<script>
import Musiclists from './MusicLists.vue'
import MusicLrc from './MusicLrc.vue'
export default {
    name:'MusicListAndLrc',
    components:{
        Musiclists,MusicLrc
    },
    props:['musicData'],
    mounted(){
        this.$bus.$on("getCurrentMusicIndex",(data)=>{
            localStorage.setItem("titleAndlrc",JSON.stringify(data))
            this.currentMusicIndex = data
        })
    },
    data(){
        return {
            currentMusicIndex:localStorage.getItem('titleAndlrc') ||0 
        }
    }
}
</script>

<style lang='less' scoped>
    .MusicListAndLrc{
        height:400px;
        width:100%;
        background: #00000050;
        flex: 1;
        color: #fbe2e0;
        border-radius: 10px;
        text-align: center;
        position: relative;
        .boxTop{
            width:100%;
            height:40px;
            line-height: 40px;
            background: #1d1d1d;
            display: flex;
            .musicList{
                width:60%;
            }
            .musicTitle{
                width:40%;
            }
            // position: absolute;
        }
        .listAndLrc{
            height:calc(100% - 40px);
            width:100%;
            display: flex;
        }
    }

</style>