<template>
  <div class="MusicLists">
    <ul class="musicListBox" ref='musicListBox'>
        <li 
            v-for="(item,index) in musicData"
            :key="item.musicTitle"
            @click='getListIndex'
            :class="{listActive: currentListMusicIndex === index}"
        >
            <div class="listInfos">
                <span class="number" v-show="currentListMusicIndex !== index || !isPlayAnimate">{{index + 1}}</span>
                <div class="jump" v-show="currentListMusicIndex === index && isPlayAnimate">
                    <div class="strips">
                        <div class="strip" style="--delay:0s"></div>
                        <div class="strip" style="--delay:0.2s"></div>
                        <div class="strip" style="--delay:0.3s"></div>
                    </div>
                   
                </div>
                <i 
                    :class='{"bi-heart-fill" : item.isCollect , "bi-heart" : !item.isCollect}' 
                    @click.stop='changeLove'
                ></i>
                <span class="musictit">{{item.musicTitle}}</span>
            </div>
            <div class="singer">
                <span>{{item.singerName}}</span>
            </div>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
    name:'MusicLists',
    props:['musicData'],
    data(){
        return {
            // 当前正在播放歌曲的索引
            currentListMusicIndex:JSON.parse(localStorage.getItem('currentMusicIndex')) || 0,
            isPlayAnimate:false,
            isLove:'',
        }
    },
    mounted(){
        this.$bus.$on('getCurrentMusicIndex',(data)=>{
            this.currentListMusicIndex = data
        })
        this.$bus.$on('playMusic',(data)=>{
            this.isPlayAnimate = data
        })
    },
    methods:{
        getListIndex(e){
            const ul = Array.from(this.$refs.musicListBox.children)
            this.currentListMusicIndex = ul.indexOf(e.currentTarget)
            this.$bus.$emit('musicListIndex',this.currentListMusicIndex)
        },
        changeLove(e){
            let icon = Array.from(this.$el.querySelectorAll('i'))
            // console.log(icon.indexOf(e.target))
            this.$bus.$emit('changeIsCollect',icon.indexOf(e.target))
        },
    },
    watch:{
        currentMusicIndex(value){
            localStorage.setItem("currentMusicIndex",JSON.stringify(value))
        }
    }
}
</script>

<style lang='less' scoped>
    .MusicLists{
        height:100%;
        width:60%;
        /* background: #baf; */
        border-right: 1px solid #888;

        .musicListBox{
            list-style: none;
            height:100%;
            overflow-y: scroll;
            font-size: 15px;
            &::-webkit-scrollbar {
            width: 3px;
            /* display: none; */
            }
            &::-webkit-scrollbar-thumb {
                border-radius: 4px;
                background-color: #999999;
            }
            li{
                height:40px;
                line-height: 40px;
                text-align:start;
                display: flex;
                // justify-content: space-between;
                .listInfos{
                    width:80%;
                    white-space: nowrap; 
                    overflow: hidden; 
                    text-overflow: ellipsis; 
                    // float:left;
                    .number{
                        display: inline-block;
                        width:30px;
                        color:#888;
                    }
                    .jump{
                        display: inline-block;
                        vertical-align: top;
                        width:30px;
                        height:40px;
                        .strips{
                            height: 40px;
                            display: flex;
                            justify-content: space-around;
                            align-items: center;
                            .strip{
                                width:4px;
                                height:20px;
                                background:#74a9d8;
                            }
                        }
                    }
                }
                .singer{
                    width:20%;
                    white-space: nowrap; 
                    overflow: hidden; 
                    text-overflow: ellipsis; 
                }
                .listInfos>*{
                    margin:0 10px 0 10px;
                }
                &:hover{
                    background: #00000030;
                    color:white;
                }
            }
            .listActive{
                background: #00000050;
                color:white;
            }
            .listActive .jump >.strips> .strip{
                transform-origin: bottom;
                animation: run 0.8s infinite linear;
                animation-delay: var(--delay);
            }
        }
        
        .bi-heart{
            color:#fbe2e0;
        }
        .bi-heart-fill{
            color:red;
        }
    }

   @keyframes run{
    0%{
        transform: scaleY(1);
    }
    50%{
        transform: scaleY(0.3);
    }
    100%{
        transform: scaleY(1);
    }
   } 
</style>