<template>
  <div class="box" ref="box">
    <ul class="left-nav">
        <li v-for="tool in tools" :key='tool.id' v-show="showLeftNav">
            <i :class="tool.icon"></i>
            <span>{{tool.toolName}}</span>
        </li>
    </ul>
    <div class="showNav" @click='colseNav' ref='showNav'>
        <i :class="leftArrow"></i>
    </div>
  </div>
</template>

<script>
export default {
    name:'leftNav',
    data(){
        return {
            tools:[  // 左侧导航栏内容
                {id:'01',toolName:'搜索',icon:'bi-search'},
                {id:'02',toolName:'定时关闭',icon:'bi-alarm-fill'},
                {id:'03',toolName:'充电中',icon:'bi-battery-charging'},
                {id:'04',toolName:'白天',icon:'bi-brightness-high-fill'},
                {id:'05',toolName:'聊天',icon:'bi-chat-dots-fill'},
                {id:'06',toolName:'游戏',icon:'bi-controller'},
                {id:'07',toolName:'设置',icon:'bi-gear-fill'},
                {id:'08',toolName:'github',icon:'bi-github'},
            ],
            showLeftNav:true,
            leftArrow:'bi-chevron-left'
        }
    },
    methods:{
        colseNav(){ // 左侧导航栏收起或展开
            this.showLeftNav = !this.showLeftNav
            if(!this.showLeftNav){
                this.$refs.box.style.width = '0'
                this.$refs.showNav.style.display = 'block'
                this.leftArrow = "bi-chevron-right"
            }else{
                this.$refs.box.style.width = '25%'
                this.leftArrow = 'bi-chevron-left'
                this.$refs.showNav.style.display = 'none'
            }
        }
    },
}
</script>

<style lang='less' scoped>
    .box{
        height: 100%;
        width:25%;
        // background: #baf;
        color:#fbe2e0;
        padding:0 1vw;
        position:relative;
        transition:.3s;
        overflow: hidden;
        margin-right:1vw;
        .left-nav{
            list-style: none;
            display: grid;
            
            grid-auto-columns: 1fr; 
            grid-template-columns: 45% 45%; 
            grid-template-rows: 22% 22% 22% 22%; 
            gap: 4% 6%; 
            grid-template-areas: 
                ". ."
                ". ."
                ". ."
                ". ."; 
            width: 100%; 
            height: 100%; 
            li{
                height:100%;
                width:100%;
                border-radius:20px;
                background-color:#00000050;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                i{
                    font-size:26px;
                    margin-bottom: 4px;
                }
                font-size:18px;
                &:hover{
                    background-color:#00000080;
                }
            }
        }
        .showNav{
            height:60px;
            width:30px;
            background-color:#00000050;
            line-height: 60px;
            position:absolute;
            right:0;
            top:50%;
            transform:translateY(-50%);
            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
            font-size:20px;
            display: none;
            text-align: center;
        }
    }
    
    .left-nav:hover + .showNav,
    .showNav:hover{
        display: block !important;
    }
            

</style>