<template>
  <div class="MainContent" ref='MainContent'>
    <MusicVisualCopy :musicData='musicData'/>
    <MusicPlayerCopy :musicData='musicData'/>
    <MusicListAndLrc :musicData='musicData'/>
  </div>
</template>

