          {musicImg:require('@/assets/img/01.jpg'),musicTitle:'若把你',singerName:'未知歌手',musicSrc:require('@/assets/audio/若把你.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[1]},
          {musicImg:require('@/assets/img/02.jpg'),musicTitle:'山茶花读不懂白玫瑰',singerName:'Lil',musicSrc:require('@/assets/audio/山茶花读不懂白玫瑰.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[2]},
          {musicImg:require('@/assets/img/03.jpg'),musicTitle:'他不懂',singerName:'张杰',musicSrc:require('@/assets/audio/他不懂.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[3]},
          {musicImg:require('@/assets/img/04.jpg'),musicTitle:'我都明白',singerName:'范倪Liu ',musicSrc:require('@/assets/audio/我都明白.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[4]},
          {musicImg:require('@/assets/img/05.jpg'),musicTitle:'岁月神偷',singerName:'金玟岐 ',musicSrc:require('@/assets/audio/岁月神偷.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[5]},
          {musicImg:require('@/assets/img/01.jpg'),musicTitle:'我曾遇到一束光',singerName:'叶斯淳 ',musicSrc:require('@/assets/audio/我曾遇到一束光.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[6]},
          {musicImg:require('@/assets/img/02.jpg'),musicTitle:'隔岸',singerName:'姚六一  ',musicSrc:require('@/assets/audio/隔岸.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[7]},
          {musicImg:require('@/assets/img/03.jpg'),musicTitle:'三拜红尘凉',singerName:'尹昔眠  ',musicSrc:require('@/assets/audio/三拜红尘凉.mp3'), isCollect:false,musicLrcData:lrcData.lrcArray[8]},
