<template>
  <div class="top-nav">
    <div class="head">
        <img src="../assets/鸡哥2.jpg">
    </div>
    <ul class="nav">
        <li v-for="item in titles" 
            :key="item.id" @mouseenter="lineMove" 
            :class="{active: currentId === item.id}" 
            @click='changeActive(item.id)'
            @mouseleave="lineStop"
        >
            <a :href="item.href">{{item.title}}</a>
        </li>
    </ul>
    <div class="set" onclick='dialog.showModal()'>
        <i class="bi bi-gear-fill"></i>
    </div>
    <div class="theme">
        <i :class="currentTheme" @click="changeTheme"></i>
    </div>
    <div class="line" ref="line"></div>

    <!-- 弹窗 -->
    <dialog id="dialog" class="animate-enter" >
        <p>这是一个对话框。</p>
        <input type="text">
        <input type="text">
        <button @click='closeDialog'>关闭</button>
    </dialog>

  </div>
</template>

<script>
export default {
    name:'HeadNav',
    data(){
        return {
            titles: [  // 导航栏内容
                { id: "01", title: "主页", href: "javascript:;" },
                { id: "02", title: "文章", href: "javascript:;" },
                { id: "03", title: "生活", href: "javascript:;" },
                { id: "04", title: "留言板", href: "javascript:;" },
                { id: "05", title: "友链", href: "javascript:;" },
                { id: "06", title: "关于", href: "javascript:;" },
            ],
            currentId:"01",  // 当前导航栏选中的内容
            currentTheme:'bi-brightness-high-fill',
        }
    },
    methods:{
        lineMove(e){ // 下划线移动
            this.$refs.line.style.left = e.target.offsetLeft +'px'
            this.$refs.line.style.width = e.target.firstElementChild.offsetWidth + 20 +'px'
            this.$refs.line.style.translate = `${e.target.firstElementChild.offsetLeft - e.target.offsetLeft - 10}px`
        },
        changeActive(id){
            this.currentId = id
        },
        lineStop(e){  // 下划线停留的位置
            this.$refs.line.style.left = this.$el.querySelector('.active').offsetLeft +'px'
            this.$refs.line.style.translate = 
            `${this.$el.querySelector('.active').firstElementChild.offsetLeft - this.$el.querySelector('.active').offsetLeft - 10}px`
            this.$refs.line.style.width = this.$el.querySelector('.active').firstElementChild.offsetWidth + 20 +'px'
        },
        changeTheme(){
            if(this.currentTheme === 'bi-brightness-high-fill'){
               this.currentTheme = 'bi-moon-stars-fill' 
            }else{
                this.currentTheme = 'bi-brightness-high-fill' 
            }
        },
        closeDialog(){ 
            dialog.close()
        },
        animateEnd(){
            console.log(dialog)
        }
    },
    mounted(){
        const active = this.$el.querySelector('.active')
        this.$refs.line.style.left = active.offsetLeft +'px'
        this.$refs.line.style.width = active.firstElementChild.offsetWidth + 20 +'px'
        this.$refs.line.style.translate = `${active.firstElementChild.offsetLeft - active.offsetLeft - 10}px`
    }
};
</script>

<style lang='less' scoped>
    .top-nav{
        height: 8vh;
        width:60vw;
        display: flex;
        justify-content: space-evenly;
        position:relative;
        .head{
            display: flex;
            align-items: center;
            img{
                height: 60%;
                aspect-ratio: 1;
                border-radius:50%;
            }
        }
        .nav{
            list-style: none;
            width:50vw;
            display: flex;
            align-items: center;
            justify-content: space-around;
            text-align: center;
            li{
                height: 100%;
                line-height: 8vh;
                width:calc(50vw / 6);
            }
            a{
                text-decoration:none;
                color:#fbe2e0;
                font-size:20px;
                font-weight: bold;
            }
        }
        .set{
            display: flex;
            align-items: center;
            color:#fbe2e0;
            font-size: 20px;
        }
        .line{
            height:5px;
            width:calc(50vw / 6);
            background:#e6a29e;
            position: absolute;
            left:0;
            bottom:1vh;
            transition: .4s;
        }
        .active{
            a{
                color:#e6a29e;
            }
        }
        .theme{
            color:#fbe2e0;
            font-size: 20px;
            line-height: 8vh;
            margin-left:1vw;
        }
    }
    #dialog{
        height: 300px;
        width:400px;
        border:none;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        border-radius: 10px;
        border:2px solid #ddd;
        text-align:center;
    }
    #dialog::backdrop{
        background:#00000050;
    }
    .dialog-show{
        border-radius: 30px;
        border:1px solid red;
    }

    
    #dialog.animate-enter {
        animation: dialog-enter 0.3s ease-out;
    }
    
    // #dialog.animate-leave {
    //     animation: dialog-leave 0.8s ease-out;
    // }

    
    @keyframes dialog-enter {
        from {
            transform: translate(-50%, -70%);
            opacity: 0;
        }
        to {
            transform: translate(-50%, -50%);
            opacity: 1;
        }
    }
    // @keyframes dialog-leave {
    //     from {
    //         transform: translate(-50%, -50%);
    //         opacity: 1;
    //     }
    //     to {
    //         transform: translate(-50%, -70%);
    //         opacity: 0;
    //     }
    // }



</style>