<template>
    <div class="MusicPlayer">
        <audio 
            :src='musicData[currentMusicIndex].musicSrc' 
            id='audio' 
            ref='audio' 
            @play='musicPlaying' 
            @pause="musicPause"
            @timeupdate="musicTimeUpdate"
            @ended='changeMusicPlayModel'
        ></audio>
        <div class="music-player">
            <div class="music-infos">
                <img :src='musicData[currentMusicIndex].musicImg' >
                <div class="infos">
                    <div class="info-title">{{musicData[currentMusicIndex].musicTitle}}</div>
                    <div class="singer-name">{{musicData[currentMusicIndex].singerName}}</div>
                </div>
            </div>

            <div class="all-tools">
                    
                <div class="player-tools">
                    <div class="control-tools">
                        <div class="play-model" @click='changePlayModelIcon'><i :class="playModel[playModeIndex]"></i></div>
                        <div class="last-music" @click="lastMusic"><i class="bi bi-skip-start-fill"></i></div>
                        <div class="play-pause" @click='playMusic'><i :class="playPause"></i></div>
                        <div class="next-music" @click="nextMusic"><i class="bi bi-skip-end-fill"></i></div>
                        <div class="love-music" @click="isLoveChange"><i :class="isLove"></i></div>
                    </div>
                    <div class="progress">
                        <span>{{beginTime}}</span>
                        <input type="range" ref='musicRange' @input="changeValue" value="1" max="100" step="1" min="1">
                        <span>{{endTime}}</span>
                    </div>
                </div>
                <div class="other-tools">
                    <div class="voice">
                        <i :class="voiceIcon" @click="showVoiceRange = !showVoiceRange"></i>
                        <input 
                            type="range" 
                            ref='voiceRange' 
                            max="100" 
                            value="100" 
                            step="1" 
                            min="0" 
                            v-show="showVoiceRange"
                            @input="adjustVolume"
                        >
                    </div>
                    <div class="musicLrc" style="font-size:16px;">词</div>
                    <div class="music-list"><i class="bi bi-music-note-list"></i></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'MusicPlayer',
    data(){
        return{
            isPlay:false, 
            playPause:'bi-caret-right-fill',
            playModel:['bi-repeat' ,'bi-repeat-1' ,'bi-shuffle'],
            playModeIndex: JSON.parse(localStorage.getItem('playModeIndex')) || 0,  
            showVoiceRange:false,
            currentMusicIndex: JSON.parse(localStorage.getItem('currentMusicIndex')) || 0, // 当前正在播放歌曲的索引
            musicCurrentTime:JSON.parse(localStorage.getItem('musicCurrentTime')) || 0, // 歌曲正在播放的位置的时间
            endTime:'',
            beginMin:"0",
            beginSec:'00',
            endMin:'',
            endSec:'',
            voiceIcon:'bi-volume-up-fill'
        }
    },
    props:['musicData'],
    methods:{
        playMusic(){
            this.isPlay = !this.isPlay
            this.isPlay ? audio.play() : audio.pause()
        },
        isLoveChange(e){ // 是否收藏歌曲
            this.$bus.$emit('changeIsCollect',this.currentMusicIndex) // 触发自定义事件后，修改musicData中的isCollect的值
        },
        changePlayModelIcon(){ // 切换播放方式，随机/顺序/循环
            this.playModeIndex ++
            if(this.playModeIndex ===3){
                this.playModeIndex = 0
            }
        },
        lastMusic(){ // 上一首
            if(this.playModeIndex !== 2){
                if(this.currentMusicIndex === 0){
                    this.currentMusicIndex = this.musicData.length - 1
                }else{
                    this.currentMusicIndex --
                }
            }else{
                let randomIndex = Math.floor(Math.random() * this.musicData.length)
                this.currentMusicIndex = randomIndex
            }
            this.$bus.$emit('getCurrentMusicIndex',this.currentMusicIndex)
            
            this.$nextTick(()=>{
                audio.play()
            })
        },
        nextMusic(){ // 下一首
            if(this.playModeIndex !== 2){
                if(this.currentMusicIndex >= this.musicData.length - 1){
                    this.currentMusicIndex = 0
                }else{
                    this.currentMusicIndex ++
                }
            }else{
                let randomIndex = Math.floor(Math.random() * this.musicData.length)
                this.currentMusicIndex = randomIndex
            }
            this.$bus.$emit('getCurrentMusicIndex',this.currentMusicIndex)
            this.$nextTick(()=>{
                audio.play()
            })
        },
        musicPlaying(){ // 播放歌曲
            this.playPause = 'bi-pause-fill'
            this.isPlay = true
            this.$bus.$emit('playMusic',this.isPlay)
        },
        musicPause(){ // 暂停歌曲
            this.playPause = 'bi-caret-right-fill'
            this.isPlay = false
            this.$bus.$emit('playMusic',this.isPlay)
        },
        changeValue(){ // 进度条
            audio.currentTime = this.$refs.musicRange.value
        },
        musicTimeUpdate(){ //歌曲播放显示已播放的时间
            this.$refs.musicRange.value = audio.currentTime
            this.musicCurrentTime = audio.currentTime
            this.beginMin = parseInt((audio.currentTime / 60) % 60)
            this.beginSec = parseInt(audio.currentTime % 60)
            if(this.beginSec < 10){
                this.beginSec = '0' + this.beginSec
            }
        },
        changeMusicPlayModel(){
            switch (this.playModeIndex){
                case 0:   // 顺序播放
                    this.currentMusicIndex ++
                    if(this.currentMusicIndex === this.musicData.length) this.currentMusicIndex =0
                    this.$nextTick(()=>{
                        audio.play()
                    })
                    break;
                case 1:   // 循环播放
                    audio.currentTime = 0
                    this.$nextTick(()=>{
                        audio.play()
                    })
                    break;
                case 2:   // 随机播放
                    let randomIndex = Math.floor(Math.random() * this.musicData.length)
                    this.currentMusicIndex = randomIndex
                    this.$nextTick(()=>{
                        audio.play()
                    })
                    break;
                case 3:
                    this.playModeIndex = 0
                    break
            }
            this.$bus.$emit('getCurrentMusicIndex',this.currentMusicIndex)
        },
        adjustVolume(){ // 调节音量
            audio.volume = this.$refs.voiceRange.value / 100
            if(audio.volume === 0){
                this.voiceIcon = 'bi-volume-mute-fill'
            }else{
                this.voiceIcon = 'bi-volume-up-fill'
            }
        }

    },
    mounted(){
        this.$bus.$emit('getCurrentMusicIndex',this.currentMusicIndex)  //通过自定义事件，将当前正在播放歌曲的索引传递给其他组件
        audio.addEventListener('loadeddata', () => {
            this.$refs.musicRange.max = audio.duration
            audio.currentTime = this.musicCurrentTime
            this.endSec = parseInt(audio.duration % 60)
            this.endMin = parseInt((audio.duration / 60) % 60)
            if(this.endSec <10){
                this.endSec = '0'+this.endSec
            }
            this.endTime = `${this.endMin}:${this.endSec}`
        });
        
        this.$bus.$on('musicListIndex',(index)=>{
            this.currentMusicIndex = index
            this.$nextTick(()=>{
                audio.play()
            })
            this.$bus.$emit('getCurrentMusicIndex',this.currentMusicIndex)
        })
    },
    computed:{
        isLove:{
            get(){
                if(this.musicData[this.currentMusicIndex].isCollect){
                    return this.isLove = "bi-heart-fill"
                }else{
                    return this.isLove = 'bi-heart'
                }
            },
            set(value){
                // if(this.musicData[this.currentMusicIndex].isCollect)
            }
        },
        beginTime:{
            get(){
                return `${this.beginMin}:${this.beginSec}`
            },
            set(value){
                console.log(value)
            }
        },
    },
    watch:{
        // 将数据保存到本地
        playModeIndex(value){
            localStorage.setItem("playModeIndex",JSON.stringify(value))
        },
        currentMusicIndex(value){
            localStorage.setItem("currentMusicIndex",JSON.stringify(value))
        },
        musicCurrentTime(value){
            localStorage.setItem("musicCurrentTime",JSON.stringify(value))
        }
    }
}
</script>

<style lang='less' scoped>
    .MusicPlayer{
        width:100%;
        background: #00000050;
        margin-bottom: 20px;
        flex:0.2;

        .music-player{
            height: 100%;
            width:100%;
            display: flex;
            // justify-content: space-between;
            // align-items: center;
            color:#fbe2e0;
            .music-infos{
                width:20%;
                // background: #bfa;
                display:flex;
                align-items: center;
                margin-right: 30px;
                img{
                    height:100px;
                    aspect-ratio: 1;
                    border-radius:5px;
                }
                .infos{
                    margin-left: 10px;
                    white-space: nowrap;
                    // color:#fbe2e0;
                    .info-title{
                        font-size:20px;
                        margin-bottom: 5px;
                    }
                    .singer-name{
                        font-size:14px;
                        color:#ada7a7;
                    }
                }
            }
            .all-tools{
                width:80%;
                display: flex;
                justify-content: space-around;
                align-items: center;

                .player-tools{
                    width:50%;
                    .control-tools{
                        display: flex;
                        justify-content: space-evenly;
                        align-items: center;
                        margin-bottom: 20px;
                        font-size:22px;
                        .play-pause{
                            width: 40px;
                            height:40px;
                            background-color:#00000040;
                            text-align: center;
                            line-height:40px;
                            border-radius:50%;
                            font-size:30px;
                            &:hover{
                                background-color:#00000080
                            }
                        }
                        &>div:not(.play-pause):hover{
                            color:#ff7575;
                        }
                    }
                    .progress{
                        display: flex;
                        justify-content: space-around;
                        
                    }
                }
                .other-tools{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;
                    height: 100%;
                    width: 20%;
                    font-size: 20px;
                    position:relative;
                    .voice{
                        display: flex;
                    }
                }
            }
        }
        input[type='range']{
            flex: 1;
            margin:0 10px;
        }
        input[type=range] {
            -webkit-appearance: none;
            background: transparent;
            width: 100%;
        }
        input[type=range]::-webkit-slider-runnable-track {
            width: 100%;
            height: 10px;
            cursor: pointer;
            animate: 0.2s;
            box-shadow: 1px 1px 1px #000000;
            background: #74a9d8;
            border-radius: 5px;
            border: 1px solid #000000;
        }
        input[type=range]::-webkit-slider-thumb {
            box-shadow: 1px 1px 1px #000000;
            border: 1px solid #000000;
            height: 20px;
            width: 15px;
            border-radius: 5px;
            background: #FFFFFF;
            cursor: pointer;
            -webkit-appearance: none;
            margin-top: -6px;
        }
    }
    .bi-heart{
        color:#fbe2e0;
    }
    .bi-heart-fill{
        color:red;
    }

</style>