<template>
  <div class="MusicLrc">
    <div class="box" ref="box" @wheel='scrollBox'>
        <ul class="lrcBox" ref="lrcBox">
            <li 
                v-for="(line,index) in LrcLineArr" 
                :key="index"
                :data-index='index'
                :class="{active: lineIndex === index }"
            >{{line.word}}</li>
        </ul>
    </div>
    
  </div>
</template>

<script>
export default {
    data(){
        return {
            currentLrcIndex:parseInt(localStorage.getItem('titleAndlrc') || 0),
            lineIndex:0,
            dataIndex:0,
            isScroll:false,
            timeoutid:'',
        }
    },
    mounted(){
        this.$bus.$on('getCurrentMusicIndex',this.parseLrcData)
        this.maxOffset = this.$refs.lrcBox.clientHeight - this.$refs.box.clientHeight
        audio.addEventListener('timeupdate',this.setLrcOffset)

    },
    props:['musicData'],
    methods:{
        parseLrcData(data){
            // console.log("当前正在播放"+ this.musicData[data].musicTitle)
            // const Lrc = this.musicData[data].musicLrcData
            this.currentLrcIndex = data

        },
        findIndex(){
            let curTime = audio.currentTime
            for(let i = 0; i< this.LrcLineArr.length; i++){
                if(curTime < this.LrcLineArr[i].time){
                    return i - 1
                }
            }
            return this.LrcLineArr.length -1
        },
        setLrcOffset(){
            let index = this.findIndex()
            let liHeight = this.$refs.lrcBox.children[0].clientHeight
            let liOffset = liHeight * index + liHeight / 2 - this.$refs.box.clientHeight / 2
            this.lineIndex = index
            if(!this.isScroll){
                this.$refs.box.scrollTop = liOffset
            }
        },
        scrollBox(){
            // 防抖函数，当鼠标滚轮滚动歌词后，三秒内没有再次滚动，则歌词回到当前正在播放的位置
            clearTimeout(this.timeoutid)
            this.isScroll = true
            this.timeoutid = setTimeout(()=>{
                this.isScroll = false    
            },3000)
        }
    },
    computed:{
        LrcLineArr(){
            // 解析歌词字符串数据，返回一个数组
            const Lrc = this.musicData[this.currentLrcIndex].musicLrcData
            const  lines = Lrc.split('\n')
            let result = []
            for(let i = 0; i<lines.length; i++){
                let lineStr = lines[i]
                let parts = lineStr.split("]")
                let timeStr = parts[0].substring(1)
                let timeResult = timeStr.split(':')
                let min = +timeResult[0] * 60
                let sec = +timeResult[1]
                let lineObj = {
                    time:min + sec,
                    word: parts[1],
                }
                result.push(lineObj)
            }
            return result
        },
    },
}
</script>

<style scoped lang='less'>
    .MusicLrc{
        height:100%;
        width:40%;
        // background: #bfa;
        .box{
            height: 100%;
            overflow-y: scroll;
            scroll-behavior: smooth;
            position: relative;
            &::-webkit-scrollbar{
                display: none;
            }
            .lrcBox{
                list-style: none;
                // height:100%;
                color:#aeabab;
                li{
                    height:40px;
                    width:80%;
                    margin:0 auto;
                    line-height: 40px;
                    transition: .3s;
                }
                .active{
                    transform:scale(1.2);
                    color:white;
                }
            }
        }
    }
</style>